.profile {
  margin: 0 auto;
  width: 260px;
  margin-top: 20px;
  text-align: center;
  border-radius: 0px 0px 4px 4px;
  background: var(--white, rgb(215, 196, 196));
  box-shadow: 0px 2px 1px 0px rgba(46, 47, 66, 0.08), 0px 1px 1px 0px rgba(46, 47, 66, 0.16), 0px 1px 6px 0px rgba(46, 47, 66, 0.08);
  }
  
   .avatar {
      width: 260px;
      height: 264px;
   }
  
