  } */
  
  .name {
      color: #061a4d;
      font-size: 24px;
      font-weight: 600;
      text-align: center;
  }
  
  .tag {
      color: #7C8097;
      font-size: .75em;
      font-weight: 300;
      text-align: center;
  
      letter-spacing: 1.5px;
      text-transform: uppercase;
  }
  
  .location {
      color: #080911;
      font-size: 14px;
      font-weight: 300;
      text-align: center;
      margin-bottom: 1.3em;
  }
  
  .stats {
      display: flex;
  padding: 10px;
  color: #061a4d;
  background-color: rgba(255,255,255,0.5);
  list-style: none;
      justify-content: space-evenly;
  }
  
  .label {
      display: flex;
        align-items: center;
        flex-direction: column;
  }